"use strict";

const WICIncomeThreshold = 1772;

const CalculateWICEligibility = function(income, numberAdults, numberChildren){
  if(income <= WICIncomeThreshold && numberChildren > 0){
    return true;
  }
  else {
    return false;
  }
};

module.exports = CalculateWICEligibility; 



